main {
    margin: 0 auto;
    max-width: 90%;
    padding: 50px 30px;
}
:root {
    --primary: #2c2b2b;
    --secondary: #2c2b2b;
    --tertiary: #2c2b2b;
    --light: #e0e2db;
    --dark: #a1a1a1;
    --darker: #111111;
    --error: rgb(228, 46, 1);
    --spacing-one: calc(1rem * 0.5);
    --spacing-two: calc(1rem);
    --spacing-five: calc(2.5rem);
}

/* custom font family Gotham */
@font-face {
    font-family: Gotham-Light;
    src: url('./assets/fonts/GOTHAM-LIGHT.TTF');
}

@font-face {
    font-family: Gotham-Bold;
    src: url('./assets/fonts/GOTHAM-BOLD.TTF');
}

* {
    box-sizing: border-box;
}

html,
body {
    min-height: 100%;
}

body {
    margin: 0;
    padding: 0;
    font-weight: 600;
    line-height: 1.5;
    font-size: 18px;
    letter-spacing: 0.1em;
    background: var(--dark);
    color: var(--light);
    font-family: 'Gotham-Light', sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Gotham-Light', sans-serif;
    font-weight: 500;
    margin: 5px 0;
    line-height: 1.25;
    color: var(--primary);
}

h1 {
    font-size: 2.25rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-family: 'Gotham-Bold';
}

h2 {
    font-size: 2rem;
}

h3 {
    font-size: 1.75rem;
}

h4 {
    font-size: 1.5rem;
}

h5 {
    font-size: 1.25rem;
}

h6 {
    font-size: 1rem;
}

p {
    font-size: 1.1rem;
    margin-top: 0;
}

a {
    color: var(--tertiary);
    text-decoration: none;
    font-weight: bold;
}

a:hover {
    text-decoration: underline;
}

header {
    background: var(--dark);
}

header a {
    color: var(--light);
}

button {
    font-size: 1rem;
    text-decoration: none;
    padding: 0.25rem 1rem;
    border-radius: 0.5rem;
    border: none;
    outline: none;
    cursor: pointer;
    margin: 0 1px;
    background-color: var(--secondary);
}

button:hover {
    opacity: 0.8;
}

button[disabled] {
    cursor: default;
    opacity: 0.8;
}

img {
    max-width: 100%;
}

span[role='img'] {
    cursor: default;
}

form {
    width: 40%;
}

input {
    font-size: 1rem;
    padding: 0.25rem;
    border: 1px solid var(--dark);
    outline: none;
    border-radius: 3px;
    transition: box-shadow 0.2s;
}

input:focus {
    border: 1px solid var(--secondary);
    box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.5);
}

ol li {
    list-style: inside;
}

li {
    display: flex;
    justify-content: center;
}

nav a:hover {
    text-decoration: none;
}

nav li {
    cursor: pointer;
}

textarea {
    width: 600px;
    height: 120px;
    border: 3px solid #cccccc;
    padding: 5px;
}

.container {
    width: 85%;
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
}

.flex-row {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.space-between {
    justify-content: space-between;
}

.flex-end {
    justify-content: flex-end;
}

.mx-1 {
    margin-right: var(--spacing-one);
    margin-left: var(--spacing-one);
}

.mx-2 {
    margin-right: var(--spacing-two);
    margin-left: var(--spacing-two);
}

.my-1 {
    margin-top: var(--spacing-one);
    margin-bottom: var(--spacing-one);
}

.my-2 {
    margin-top: var(--spacing-two);
    margin-bottom: var(--spacing-two);
}

.my-5 {
    margin-top: var(--spacing-five);
    margin-bottom: var(--spacing-five);
}

.px-1 {
    padding-right: var(--spacing-one);
    padding-left: var(--spacing-one);
}

.px-2 {
    padding-right: var(--spacing-two);
    padding-left: var(--spacing-two);
}

.py-1 {
    padding-top: var(--spacing-one);
    padding-bottom: var(--spacing-one);
}

.py-2 {
    padding-top: var(--spacing-two);
    padding-bottom: var(--spacing-two);
}

.card {
    width: 25%;
    text-align: center;
}

.img-thumbnail {
    width: auto;
    height: 300px;
    margin-bottom: 50px;
    cursor: pointer;
}

.modalBackdrop {
    background: rgba(0, 0, 0, 0.65);
    bottom: 0;
    left: 0;
    overflow: auto;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 1;
}

.modalContainer {
    background: var(--darker);
    border-radius: 5px;
    max-width: 100%;
    margin: 50px auto;
    padding: 15px;
    width: 960px;
}

.modalTitle {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 25px;
}

button {
    background: var(--primary);
    border: 0;
    border-radius: 5px;
    color: #e0e2db;
    cursor: pointer;
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 30px;
    padding: 10px 15px;
}

a {
    border-radius: 5px;
    color: var(--primary);
    font-weight: 600;
    padding: 2px 4px;
    margin-left: -2px;
    margin-right: -2px;
}

.navActive {
    color: var(--secondary);
}

#resume-pdf {
    color: var(--secondary);
    text-decoration: underline;
}

.project-title {
    color: var(--secondary);
}

.center {
    display: flex;
    justify-content: center;
}

.card {
    width: 25%;
    text-align: center;
    margin: 0.3em;
    padding: 0.3em;
}

.card-title {
    font-size: 1.5rem;
    color: var(--primary);
    font-family: 'Gotham-Bold';
}

.card-subtitle {
    font-size: 1rem;
    color: var(--tertiary);
    margin: auto;
    font-family: 'Gotham-Bold';
}

.card-text {
    font-size: 0.75rem;
    color: var(--light);
}

.card-techs {
    font-size: 0.75rem;
    color: var(--light);
}

.card-image {
    max-width: 300px;
    max-height: 150px;
}

.card-link {
    font-family: 'Gotham-Light', sans-serif;
    font-size: 0.8rem;
}
